<template>
    <div class="tstuwork">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-c-scale-to-original"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{items.teaching.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container"  v-loading="loading">
            <div class="form">
                <h3>{{items.teaching.name}}</h3>
                <el-form ref="form" label-width="90px">
                    <el-form-item class="work-date" size="small" label="指导老师：">
                        {{items.teaching.teacherName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="学生：">
                        {{items.studentName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="作业项目：">
                        {{items.workProjectInfoDTO.name}}
                    </el-form-item>
                    <el-form-item label="项目简介：">
                        <div class="teacherishi">{{items.workProjectInfoDTO.desc}}</div>
                    </el-form-item>
                    <el-form-item label="相关资料：">
                        <template v-for="item in items.workProjectInfoDTO.filelist">
                        <a href="" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="工艺卡片：">
                        <el-form v-if="status==1||status==3" v-for="item in craftCardInfoDTOList" ref="form" label-width="85px" class="form-in">
                          <h4>工序{{item.sort+1}}</h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              {{item.surfaceName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                              {{item.processingMethodName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="工序内容：">
                              <div class="teacherishi">{{item.processContent}}</div>
                          </el-form-item>
                        </el-form>

                        <el-form v-if="status==0||status==2"  ref="form" v-for="(item, index) in craftCardInfoDTOList" label-width="85px" class="form-in">
                          <h4>工序{{index+1}}   <el-button :disabled="craftCardInfoDTOList.length<2"  class="del-button" size="mini" @click="delcarts(index)" type="danger" plain>删除卡片</el-button></h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              <el-select size="mini" v-model="item.surfaceId" placeholder="请选择加工表面">
                                <el-option
                                  v-for="item in itemsbm"
                                  :key="item.id"
                                  :label="item.name"
                                  :value="item.id">
                                </el-option>
                              </el-select>
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                              <el-select size="mini" v-model="item.processingMethodId" placeholder="请选择加工形式">
                                <el-option
                                  v-for="item in itemsxs"
                                  :key="item.id"
                                  :label="item.name"
                                  :value="item.id">
                                </el-option>
                              </el-select>
                          </el-form-item>
                          <el-form-item class="work-date work-date-cont" size="small" label="工序内容：">
                              <div class="modify-bg">
                                <div class="modify-top">
                                    <el-input
                                      type="textarea"
                                      placeholder="请输入内容"
                                      v-model="item.processContent"
                                      maxlength="300"
                                      :autosize="{ minRows: 3}"
                                      show-word-limit
                                    ></el-input>
                                </div>
                                <div class="modify">
                                  <p><el-button type="primary" :disabled="index==0" size="mini" plain @click="removefront(index)"> 向前移动 </el-button></p>
                                  <p><el-button :disabled="index+1==craftCardInfoDTOList.length" type="primary" size="mini" plain @click="removeafter(index)"> 向后移动 </el-button></p>
                                  <p><el-button type="primary" size="mini" plain @click="addafter(index)"> 后方插入 </el-button></p>
                                </div>
                              </div>
                          </el-form-item>
                        </el-form>
                    </el-form-item>
                    <el-form-item label="作业讨论：">

                        <div class="talk-bg" v-for="(item, index) in workMessageInfoDTOList">
                          <div class="talk">
                            <div class="fl pic">
                              <img src='../../assets/img/student.jpg' alt="" v-if="item.senderType==1" />
                              <img v:else src='../../assets/img/teacher.jpg' alt="" />
                            </div>
                            <p class="fl">{{item.senderName}}   {{item.createtime}}</p>
                          </div>
                          <p :class="[item.senderType==1?'text self green':'text self']">{{item.content}}<i class="el-icon-close deldiscuss" v-if="item.senderType==1" @click="deletemessage(item.id,index)"></i></p>
                        </div>

                        <el-input
                          class="push-talk"
                          type="textarea"
                          placeholder="请输入内容"
                          size="small" 
                          v-model="talktext"
                          rows="4" 
                          maxlength="300"
                          show-word-limit
                        ></el-input>
                        <p class="push-button"><el-button size="small" @click="Addmessagebystudent" type="primary">发表</el-button></p>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="savebg">
          <div class="savein">
            <template v-if="status==0||status==2">
              <p class="fl"><el-button size="small" type="primary" @click="savework(0)">保存</el-button> 正在自动保存...</p>
              <p class="fr"><el-button size="small" @click="submitwork" type="primary">保存并提交</el-button> 请于{{items.teaching.deadlineTime}} 之前提交作业</p>
            </template>
            <template v-if="status==1">
              <p class="fr">
                <el-alert
                  title="作业已提交"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
              </p>
            </template>
            <template v-if="status==3">
              <p class="fr">
                <el-alert
                  title="作业已完成"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
              </p>
            </template>
          </div>
        </div>
    </div>
</template>

<script>
import {fetchGetstudentworkinfobyid, fetchGetallprocessingmethod, fetchGetallsurface, fetchAddcraftcardlist, fetchAddmessagebystudent, fetchDeletestudentmessage, fetchSubmit} from '../../axios/requests'
export default {
    name: 'course',
    data() {
        return {
          status:0,
          loading:false,
          id:'',
          items:'',
          craftCardInfoDTOList:[],//卡片数组
          workMessageInfoDTOList:[],//评论数组
          itemsbm:[],
          itemsxs:[],

          talktext:'',
          timer:''
        };
    },
    mounted(){
      let {id} = this.$route.params;
      this.id=id;
      this.Getallsurface();
      this.Getallprocessingmethod();
      this.Getworkinfobyid();
    },
    methods: {
      timers() {//定时保存
　　　　this.timer = setInterval( () => {
          this.savework(2)
　　　　}, 10000);
      },
      addafter(index){//向后添加
        let data = {'surfaceId':'','processingMethodId':'','processContent':'','id':'','sort':''};
        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      delcarts(index){//删除卡片
        this.craftCardInfoDTOList.splice(index, 1)
      },
      removefront(index){//向前移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);
        this.craftCardInfoDTOList.splice(index-1, 0, data)
      },
      removeafter(index){//向后移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);

        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      viewwork(){
        this.$router.push({path:'workdetail'});
      },
      // 保存并提交
      submitwork() {
          var goif = false;
        this.craftCardInfoDTOList.forEach((item, index) =>{
          if (!item.surfaceId) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'选择加工表面',
              type: 'warning'
            });
            goif = true;
            return;
          }
          if (!item.processingMethodId) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'选择加工形式',
              type: 'warning'
            });
            goif = true;
            return;
          };
          if (!item.processContent) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'填写工序内容',
              type: 'warning'
            });
            goif = true;
            return;
          }
        })
        if (goif) {
          return;
        };
          this.$confirm('确定提交？提交完成不能修改', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.savework(1);
          })
          .catch(() => {});
      },
      deletemessage(id, index){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage(id, index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage(id, index) {//删除消息
        this.loading = true;
        try {
          let response = await fetchDeletestudentmessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Submits(){//提交作业
        this.loading = true;
        try {
          let response = await fetchSubmit(this.id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '作业提交成功',
              type: 'success'
            });
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async savework(type){//保存作业
        var list = this.craftCardInfoDTOList.map((r, index)=>{
          var ite = {
            'craftCardId':r.id,
            'surfaceId':r.surfaceId,
            'processingMethodId':r.processingMethodId,
            'processContent':r.processContent,
            'sort':index
          }
          return ite;
        })
        this.loading = true;
        try {
          let response = await fetchAddcraftcardlist(this.id, list);
          if (response.status.code ==0) {
            if (type==1) {
              this.Submits()
            }else if(type==0){
              this.$message({
                showClose: true,
                message: '作业保存成功',
                type: 'success'
              });
            }else{

            }
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getworkinfobyid() {//得到作业详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetstudentworkinfobyid(id);
          if (response.status.code ==0) {
            this.items = response.result;
            this.workMessageInfoDTOList = response.result.workMessageInfoDTOList;
            this.status = response.result.status;
            if (response.result.status==0||response.result.status==2) {
              this.timers();
            };
            if (response.result.craftCardInfoDTOList.length==0) {
              this.craftCardInfoDTOList = [{
                'surfaceId':'',
                'processingMethodId':'',
                'processContent':'',
                'id':'',
                'sort':''
              }]
            }else{
              this.craftCardInfoDTOList = this.$sortBykey(response.result.craftCardInfoDTOList,'sort');
            }
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Addmessagebystudent() {//添加作业消息
        let {id, talktext} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddmessagebystudent(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getallsurface() {//得到加工表面
        try {
          let response = await fetchGetallsurface();
          if (response.status.code ==0) {
            this.itemsbm = response.result;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      async Getallprocessingmethod() {//得到加工形式
        try {
          let response = await fetchGetallprocessingmethod();
          if (response.status.code ==0) {
            this.itemsxs = response.result;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
    }
};
</script>

<style lang='scss'>
  .form-in{
    line-height:28px;
    h4{
      padding-left:4px;
    }
    .el-form-item__label,.el-form-item__content{
      line-height:36px;
    }
  }
</style>

<style lang='scss' scoped>
    .tstuwork{
      // position: relative;
      .crumbs{
          padding-left:20px;
        }
        .savebg{
          position:fixed;
          bottom:0;
          height:47px;
          padding:0 28px 0 11px;
          left:0;
          box-sizing: border-box;
          width:100%;
          .savein{
            padding:5px 20px;
            background:#fff;
            box-sizing: border-box;
            width:100%;
            height:47px;
            color:#888;
            font-size:14px;
          }
        }
        .modify-bg{
          display:flex;
          .modify-top{
            flex:1;
            min-width:0;
          }
          .modify{
            width:100px;
            margin-top:-37px;
            padding-left:20px;
          }
        }
        h4 .del-button{
          float:right;
          margin-right:20px;
          margin-top:10px;
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
            margin-bottom:15px;
          }
          .teacherishi{
            line-height:22px;
            padding-top:9px;
          }
          .download-bg{
            color:#409EFF;
            .download{
              padding:0px;
            }
          }
          .form-in{
            border:1px solid #ccc;
            padding:5px 15px 10px;
            background:#f9f9f9;
            margin-top:10px;
            .teacherishi{
              padding-top:5px;
            }
          }
          .talk-bg{
            margin-top:15px;
            
            .talk{
              overflow:hidden;
              line-height:24px;
            }
            div.pic{
              width:24px;
              height:24px;
              background:#ccc;
              border-radius:24px;
              margin-right:6px;
            }
            div.pic img{
              display:block;
              width:24px;
              height:24px;
              border-radius:24px;
            }
            p.fl{
              color:#666;
              font-size:12px;
            }
            .text{
              background:#ffeeee;
              border:1px solid #ffb8b8;
              border-radius:5px;
              width:60%;
              padding:10px 15px;
              margin-left:30px;
              line-height:22px;
              box-sizing:border-box;
            }
            .green{
              background:#f2ffeb;
              border:1px solid #a3e881;
            }
            .self{
              padding-right:30px;
              position: relative;
            }
            .deldiscuss{
              position:absolute;
              right:4px;
              top:4px;
              font-size:16px;
              cursor:pointer;
            }
          }
          .push-talk{
            width:62%;
            margin-left:30px;
            margin-top:30px;
            border:1px solid #ccc;
            border-radius:6px;
          }
          .push-button{
            padding-left:30px;
            margin-top:10px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
    }
</style>


